const math = require("mathjs");

var ML = {
  Common: Common,
  LogReg: LogReg,
  NN: NN
};

// ========================================================================
// =========================== Common =====================================
// ========================================================================
/**
 * Common ML
 *
 * 
 * 1. inputTrainRaw: 
 * 2. inputCS2Mat: 
 * 3. inputClean: 
 * 4. featureEngineering: 
 * 5. featureScaling:  
 * 6. modelTrainCV: 
 * 7. modelTest: 
 * 8. visulization: 
 *
 */
function Common() {
  this.Procedure = [
    "inputTrainRaw",
    "inputCS2Mat",
    "inputClean",
    "featureEngineering",
    "featureScaling",
    "modelTrainCV",
    "modelTest( testData )",
    "visulization"
  ];
}

Common.prototype = {
  inputTrainRaw: function(inp) {
    this.inputTrainRaw = inp;
    return this;
  },
  /**
   * canvasinputTrainRaw  inputX, inputY
   *
   * inputX: Features, inputXmodel
   * inputY: Labels, 1
   */
  inputCS2Mat: function() {
    var { X, Y } = this.canvas2MLmat(this.inputTrainRaw);
    this.inputX = X;
    this.inputY = Y;
    return this;
  },
  /**
   *  onehot.
   * canvas inputX, onehot inputY
   * inputX 12 Features
   * inputY  33onehot
   */
  inputCS2MatXOneHotY: function() {
    var { X, Y } = this.canvas2MLmatOneHot(this.inputTrainRaw);
    this.inputX = X;
    this.inputY = Y;
    return this;
  },
  /**
   * canvas
   * [ {pos: [x, y], type:"A | B | ...", color: "", classes: }, {},...]
   *  inputX 2  3
   *  inputY 33  math.matrix
   * @param {*} inputTrainRaw
   */
  canvas2MLmatOneHot: function(inputTrainRaw) {
    var classes = inputTrainRaw[inputTrainRaw.length - 1].classes;

    var xArr = [];
    var yArr = [];
    inputTrainRaw.forEach(item => {
      var pos = item.pos;
      xArr.push(pos);

      var type0 = this.labelOfType(item.type); //  [0],[1],[2],...
      var type = type0[0]; // 0,1,2,3,...
      var zeros = this.zerosOne(classes, type);
      yArr.push(zeros);
    });

    var X = math.matrix(xArr);
    var Y = math.matrix(yArr);
    return { X: X, Y: Y };
  },

  /**
   * onehot
   * @param {*} classes 
   * @param {*} idx idx10
   */
  zerosOne: function(classes, idx) {
    var zeros = [];
    for (let i = 0; i < classes; i++) {
      if (i != idx) {
        zeros.push(0);
      } else {
        zeros.push(1);
      }
    }
    return zeros;
  },

  inputClean: function() {
    return this;
  },
  featureEngineering: function() {
    return this;
  },
  /**
   *  inputX ,
   * 
   * 1. (data-min)/(max-min) inputXScaleMinVec, inputXScaleMaxVec;
   * 2. (data-mean)/std,  inputXScaleMeanVec, inputXScaleStdVec;
   *
   * Vec[ a,b,c,...]
   * @param scaleType 12
   */
  featureScaling: function(type) {
    this.scaleType = type;
    this.inputX = this.scaling(this.inputX, type);
    return this;
  },
  modelTrainCV: function() {
    return this;
  },
  modelTest: function() {
    return this;
  },
  visulization: function() {
    return this;
  },
  /**
   * test.
   *
   * test
   * 
   */
  testDataFromCanvas: function(csWidth, csHeight) {
    var tempArr = [];
    for (var col = 0; col < csWidth; col++) {
      for (var row = 0; row < csHeight; row++) {
        tempArr.push([col, row]);
      }
    }

    var testData = math.matrix(tempArr);
    return testData;
  },
  /**
   *  scaling 
   *  [[ a, b ],[ c, d ],...] - [ [ aa, bb ] ] 
   * @param {*} inp , math.matrix() , 
   * @param {*} dir 0 1
   * @param {*} num 
   */
  extendArr: function(inp, dir, num) {
    var arrNew;
    if (dir == 0) {
      arrNew = inp.clone();
      while (arrNew.size()[0] < num) {
        arrNew = math.concat(arrNew, inp, 0);
      }
    } else {
      arrNew = inp.clone();
      while (arrNew.size()[1] < num) {
        arrNew = math.concat(arrNew, inp);
      }
    }

    return arrNew;
  },
  /**
   * math math.std()  
   * 
   * @param {*} inp math.matrix 
   * @param {*} dir 0: 
   * @returns math vector   [ a,b,...]
   */
  myStd: function(inp, dir) {
    var direc = dir || 0;
    if (direc === 0) {
      // 
      var rows = inp.size()[0];
      var cols = inp.size()[1];
      var listStd = [];
      for (var i = 0; i < cols; i++) {
        // inpi
        var iColData = math.subset(inp, math.index(math.range(0, rows), i));
        var tempStd = math.std(iColData); // math.std 
        listStd.push(tempStd);
      }
      return math.matrix(listStd);
    }
  },
  /**
   * scaling0
   * 
   * 1.
   */
  handleDenIs0InScaling: function(out, den) {
    // den0 den01
    var idx = null;
    math.forEach(den, (item, index) => {
      if (index[0] != 0) {
        return;
      }
      if (item == 0) {
        // console.log("index:", index);
        idx = index; // []
      }
    });
    if (idx != null) {
      var colIs0 = idx[1]; //  colIs0
      var outNew = math.subset(
        out,
        math.index(math.range(0, out.size()[0]), colIs0),
        // out1outNaN
        math.ones(out.size()[0], 1)
      );
      return outNew;
    } else {
      return out;
    }
  },
  /**
   * ;
   * ;
   * 
   * 1. (data-min)/(max-min)
   * 2. (data-mean)/std
   * @param inp 
   * @param scaleType maxmin, meanstd
   * @returns  scaling
   */
  scaling: function(inp, scaleType) {
    var type = scaleType || 1;
    var out;
    if (type === 1) {
      var minVal = math.min(inp, 0); //  [a,b,...]
      var minValNew = math.reshape(minVal, [1, minVal.size()[0]]); // minValNew: math.matrix 
      var maxVal = math.max(inp, 0);
      var maxValNew = math.reshape(maxVal, [1, maxVal.size()[0]]); // maxVal: math.matrix 

      //  minVal, maxVal 
      var minArr = this.extendArr(minValNew, 0, inp.size()[0]);
      var maxArr = this.extendArr(maxValNew, 0, inp.size()[0]);
      var inpMinusMinArr = math.subtract(inp, minArr);
      var range = math.subtract(maxArr, minArr);

      out = math.dotDivide(inpMinusMinArr, range);
      out = this.handleDenIs0InScaling(out, range);
      this.inputXScaleMinVec = minVal;
      this.inputXScaleMaxVec = maxVal;
      return out;
    } else {
      var meanVal = math.mean(inp, 0); // mathvector
      var stdVal = this.myStd(inp, 0); // mathvector
      var meanValNew = math.reshape(meanVal, [1, meanVal.size()[0]]);
      var stdValNew = math.reshape(stdVal, [1, stdVal.size()[0]]);
      // extend
      var meanArr = this.extendArr(meanValNew, 0, inp.size()[0]);
      var stdArr = this.extendArr(stdValNew, 0, inp.size()[0]);
      // console.log("stdArr:", stdArr);
      var inpMinusMean = math.subtract(inp, meanArr);
      out = math.dotDivide(inpMinusMean, stdArr);
      out = this.handleDenIs0InScaling(out, stdArr);
      this.inputXScaleMeanVec = meanVal;
      this.inputXScaleStdVec = stdVal;
      return out;
    }
  },
  /**
   * @param {} inpZ  math.matrix  
   */
  sigmoid: function(inpZ) {
    // h = 1 / (1 + Math.E ** -z)
    var num = 1;
    var expMinusZ = math.dotPow(Math.E, math.dotMultiply(-1, inpZ));
    var den = math.add(1, expMinusZ); //  elementwisedot
    var out = math.dotDivide(num, den);
    return out;
  },
  /**
   * canvasjsontype10
   */
  labelOfType: function(type) {
    switch (type) {
      case "A":
        return [0];
      case "B":
        return [1];
      case "C":
        return [2];
      case "D":
        return [3];
      case "E":
        return [4];
      case "F":
        return [5];
      case "G":
        return [6];
      case "H":
        return [7];
      case "I":
        return [8];
      case "J":
        return [9];
      default:
        return null;
    }
  },

  /**
   * canvas[ {pos: [x, 1], type:"", color: ""}, {},...]
   *
   * : { X: X, Y: Y } MLmatrix
   * 1. XFeatures, [[1,x,y],[1,x,y],...]
   * 2. Y: Labels, [[0],[1],...]
   */
  canvas2MLmat: function(canvasData) {
    // canvasData: canvas
    // [
    // { pos: [x, y], type: "A", color: "" },
    // {},
    // {},
    // ...
    // ]
    // out: ML():
    // X: math.matrix, [[1,x1,x2],[1,x1,x2],[1,x1,x2],...]
    // Y: math.matrix, [[0 | 1],[0 | 1],[],...]
    var X0 = new Array();
    var Y0 = new Array();
    canvasData.forEach(item => {
      var pos = item.pos;
      var type = item.type;
      X0.push(pos);
      Y0.push(this.labelOfType(type));
    });
    var X = math.concat(math.ones(X0.length, 1), X0);
    var Y = math.matrix(Y0);
    return { X: X, Y: Y }; // json
  }
};

// ========================================================================
// =========================== Optimizer ==================================
// ========================================================================
/**
 * Optimizer  W = W - alpha* dCdW  W
 *  dCdW
 *
 * alpha, dCdW
 * @returns  W
 *
 *  
 * 0. Gradient Descent 
 * 1. RMSProp
 * 2. Adadelta
 */
function Optimizer() {
  this.pho = 0.9; // 
  this.epsilon = 10 ** -8; // 0.
}

Optimizer.prototype = {
  /**
   *
   * @param {*} oldW Wmath.matrix
   * @param {*} stepSize 
   * @param {*} gt gradient of , math.matrix
   * @returns  Wmath.matrix
   */
  GradientDescent: function(oldW, stepSize, gt) {
    var W = math.subtract(oldW, math.multiply(stepSize, gt));
    // var newW = curW - stepSize * gt;
    return W;
  },
  /**
   * 
   * dwt = - alpha  /  RMS[ g ]_ t  * gt
   *
   * rmsProp gtgt
   * @param {*} oldW Wmath.matrix
   * @param {*} stepSize 
   * @param {*} gt  gmath.matrix 1
   * @param {*} gt_1 garr
   * @returns W
   */
  RMSProp: function(oldW, stepSize, gt, gt_1Arr) {
    // gt_1ArrgLogReg

    this.pho = 0.9;
    this.epsilon = 10 ** -8;
    var W;
    // gt_1Arr.
    if (gt_1Arr.length == 0) {
      W = this.GradientDescent(oldW, stepSize, gt);
      return W;
    }

    var rms_gt = this.calcRMSgt(gt, gt_1Arr, this.pho, this.epsilon);

    // var dxt = (-stepSize * gt) / rms_gt;
    var dxt = math.dotDivide(math.multiply(-stepSize, gt), rms_gt);
    // var W = oldW + dxt; W dxt
    W = math.add(oldW, dxt);
    return W;
  },
  /**
   * Adadelta  Adagrad, RMSProp ;
   * Adadelta ;
   *
   *
   * dwt = - RMS[ dx ]_ t-1  /  RMS[ g ]_ t  * gt
   *
   * @param oldW 31
   * @param dxArr 31
   */
  Adadelta: function(oldW, stepSize, gt, gt_1Arr, dxArr) {
    this.pho = 0.9;
    this.epsilon = 10 ** -8;
    // 12dxArr  Adadelta 

    if (gt_1Arr.length == 0) {
      W = this.GradientDescent(oldW, stepSize, gt);
      var dW01 = math.subtract(W, oldW);
      return { W: W, dW: dW01 };
    }
    if (gt_1Arr.length == 1) {
      W = this.RMSProp(oldW, stepSize, gt, gt_1Arr);
      dW01 = math.subtract(W, oldW);
      return { W: W, dW: dW01 };
    }

    var rms_gt = this.calcRMSgt(gt, gt_1Arr, this.pho, this.epsilon);

    var dxArrnew = math.matrix(dxArr);
    var size0 = dxArrnew.size()[0];
    var size1 = dxArrnew.size()[1];
    var size2 = dxArrnew.size()[2];

    var curdx_temp = dxArrnew.subset(
      math.index(size0 - 1, math.range(0, size1), math.range(0, size2))
    );
    var curdx = math.matrix(curdx_temp.valueOf()[0]);

    var boforedxArr_temp = dxArrnew.subset(
      math.index(
        math.range(0, size0 - 1),
        math.range(0, size1),
        math.range(0, size2)
      )
    );

    var beforedxArr = boforedxArr_temp.valueOf();
    var rms_dxt_1 = this.calcRMSgt(curdx, beforedxArr, this.pho, this.epsilon);

    var dxt_num = math.multiply(-1, math.dotMultiply(rms_dxt_1, gt));
    var dxt_den = rms_gt;
    var dxt = math.dotDivide(dxt_num, dxt_den);
    var W = math.add(oldW, dxt);
    return { W: W, dW: dxt };
  },
  /**
   * RMS rmsProp, adadelta 
   * 1. E[ g^2 ]_ t = pho * E[ g^2 ]_ (t-1) + (1-pho)*(g_t)^2
   * 2. RMS[ g ]_t = sqrt( E[ g^2 ]_t + epsilon )
   * @param {*} curGt  gradient dCdWmath.matrix
   * @param {*} beforeGArr 31math.matrix
   * @param {*} pho  0.9
   * @param {*} epsilon  1e-8, 0
   */
  calcRMSgt: function(curGt, beforeGArr, pho, epsilon) {
    var len = curGt.size()[0];
    //  math.matrixgt_1Arr 31
    //  E[g^2]_(t-1)  powmean elemetwise reshape
    var g2t_1 = math.dotPow(math.matrix(beforeGArr), 2);
    var g2t_1_reshape = math.reshape(g2t_1, [beforeGArr.length, len]); // reshape
    var E_g2t_1_old = math.mean(g2t_1_reshape, 0);
    var E_g2t_1 = math.reshape(E_g2t_1_old, [len, 1]); //  ggt 31
    // console.log("E_g2t_1:", E_g2t_1);

    var g2t = math.dotPow(curGt, 2); // gt  31
    // console.log("g2t:", g2t);
    // console.log("this.pho:", this.pho);
    // var E_g2t = this.pho * E_g2t_1 + (1 - this.pho) * g2t;
    var E_g2t = math.add(
      math.dotMultiply(pho, E_g2t_1),
      math.dotMultiply(math.subtract(1, pho), g2t)
    );
    var rms_gt = math.sqrt(math.add(E_g2t, epsilon));
    return rms_gt;
  }
};
// ========================================================================
// =========================== LogReg =====================================
// ========================================================================
/**
 * 
 * 1. inputTrainRaw( inp ),
 * 2. inputCS2Mat(),
 * 3. inputClean(),
 * 4. featureEngineering(),
 * 5. featureScaling( 1 as default | 2 ),
 * 6. modelTrainCV(),
 * 7. modelTest(),
 * 8. visulization()
 */
function LogReg() {}

// LogRegCommon
for (let i in Common.prototype) {
  LogReg.prototype[i] = Common.prototype[i];
}

for (let j in Optimizer.prototype) {
  LogReg.prototype[j] = Optimizer.prototype[j];
}

/**
 *
 * @param {*} stepSize 0.1 as default
 * @param {*} stepTotal 100 as default
 * @param {*} optimizer . 0: GD as default, 1: RMSProp, 2: Adadelta
 * @param {*} isLogW Wfalse as default
 * @param gxLen  RMSProp, Adadeltagradient,dW
 */
LogReg.prototype.modelTrainCV = function(
  stepSize,
  stepTotal,
  optimizer,
  isLogW,
  gxLen
) {
  this.stepSize = stepSize || 0.1;
  this.stepTotal = stepTotal || 100;
  this.optimizer = optimizer || "GD";
  this.isLogW = isLogW || false;
  this.gxLen = gxLen || 100;
  var X = this.inputX;
  var Y = this.inputY;
  // 
  var W = math.ones(X.size()[1], 1); // 
  var XT = math.transpose(X);
  this.logWval = [W.valueOf()]; // W
  // W
  var dCdWArr = []; //  dCdW gt.
  var dWArr = []; //  dW Adadelta
  for (var i = 0; i < this.stepTotal; i++) {
    var Z = math.multiply(X, W); // Z = XW

    var H = this.sigmoid(Z);
    var HminusY = math.subtract(H, Y);
    var dCdW = math.multiply(XT, HminusY); // ML dCdW 
    // console.log("dCdW:", dCdW);

    if (optimizer == "GD" || optimizer == 0) {
      // , W -= alpha * dCdW
      // W = math.subtract(W, math.multiply(this.stepSize, dCdW));
      W = this.GradientDescent(W, this.stepSize, dCdW);
    } else if (optimizer == "RMSProp" || optimizer == 1) {
      // RMSProp
      W = this.RMSProp(W, this.stepSize, dCdW, dCdWArr); // 1
      dCdWArr.push(dCdW.valueOf());

      // dCdW
      while (dCdWArr.length > this.gxLen) {
        dCdWArr.shift();
      }
    } else if (optimizer == "Adadelta" || optimizer == 2) {
      var obj = this.Adadelta(W, this.stepSize, dCdW, dCdWArr, dWArr);
      W = obj.W;
      var dW = obj.dW;

      dCdWArr.push(dCdW.valueOf());
      while (dCdWArr.length > this.gxLen) {
        dCdWArr.shift();
      }

      dWArr.push(dW.valueOf());
      while (dWArr.length > this.gxLen) {
        dWArr.shift();
      }
    }
    if (this.isLogW) {
      // console.log("i: " + i + ", dCdWArr.length: " + dCdWArr.length);
      // 
      this.logWval.push(W.valueOf());
    }
  }
  this.optW = W;
  this.optWval = W.valueOf();
  this.optWdetails = this.calcWdetails(this.optWval);
  return this;
};

LogReg.prototype.modelTest = function() {
  return this;
};
LogReg.prototype.getOptWval = function() {
  return this.optWval;
};
LogReg.prototype.getOptWdetails = function() {
  return this.optWdetails;
};
/**
 * W
 *
 * : Ww0 w1 w2 x1
 *
 * 2features3wW
 *
 * x2 = coefficientOfX1 * x1 + x2Intercept
 *
 * WvalWval
 *  x2~x1
 */
LogReg.prototype.calcWdetails = function(aWval) {
  var w0 = aWval[0][0];
  var w1 = aWval[1][0];
  var w2 = aWval[2][0];
  var coefficientOfX1 = -w1 / w2;
  var x2Intercept = -w0 / w2; // value of x2, when x1 = 0 
  var x1Intercept = -w0 / w1; // value of x1, when x2 = 0 
  return {
    w0: w0,
    w1: w1,
    w2: w2,
    coefficientOfX1: coefficientOfX1,
    x2Intercept: x2Intercept,
    x1Intercept: x1Intercept
  };
};

/**
 * wArr  31
 * @returns costArr, [ v1,v2, v3,... ]
 */
LogReg.prototype.calcCostArr = function(wArr, X, Y) {
  // var wArr = this.logWval;
  // var X = this.inputX;
  // var Y = this.inputY;
  var costArr = [],
    Z,
    H;
  var left, right, temp1, temp2, cost;
  for (var i = 0; i < wArr.length; i++) {
    Z = math.multiply(X, wArr[i]);
    H = this.sigmoid(Z);
    // left = -Y* log(H)
    left = math.dotMultiply(math.multiply(-1, Y), math.log(H));

    // right = -(1-Y)*log(1-H)
    temp1 = math.subtract(Y, 1);
    temp2 = math.log(math.subtract(1, H));
    right = math.dotMultiply(temp1, temp2);
    cost = math.add(left, right); // cost 1

    // cost
    var cost_mean = math.mean(cost);

    costArr.push(cost_mean);
  }
  return costArr;
};

LogReg.prototype.calcOneCost = function(w, X, Y) {
  var Z, H;
  var left, right, temp1, temp2, cost;
  Z = math.multiply(X, w);
  H = this.sigmoid(Z);
  left = math.dotMultiply(math.multiply(-1, Y), math.log(H));
  // right = -(1-Y)*log(1-H)
  temp1 = math.subtract(Y, 1);
  temp2 = math.log(math.subtract(1, H));
  right = math.dotMultiply(temp1, temp2);
  cost = math.add(left, right);
  return cost;
};

// ========================================================================
// =========================== NN =========================================
// ========================================================================
/**
 * 
 * 1. inputTrainRaw( inp ),
 * 2. inputCS2MatXOneHotY()
 * 3. inputClean(),
 * 4. featureEngineering(),
 * 5. featureScaling( 1 as default | 2 ),
 *
 * 6. modelTrainCV(),
 * 7. modelTest(),
 * 8. visulization()
 */
function NN() {}

for (let i in Common.prototype) {
  NN.prototype[i] = Common.prototype[i];
}

for (let j in Optimizer.prototype) {
  NN.prototype[j] = Optimizer.prototype[j];
}

NN.prototype.softmax = function(inpZ) {
  var cols = inpZ.size()[1];
  // Zmaxexp
  var maxCurRow = math.max(inpZ, 1);
  var maxCurRow_reshape = math.reshape(maxCurRow, [maxCurRow.size()[0], 1]);
  var maxCurRow_reshape_clone = this.extendArr(maxCurRow_reshape, 1, cols); // inp, dir, num
  // console.log("inpZ: ", inpZ);
  // console.log("maxCurRow_reshape_clone:", maxCurRow_reshape_clone);
  var inpZ_shift = math.subtract(inpZ, maxCurRow_reshape_clone);
  // Z exp(z)
  //  expZ  sum
  var expZ = math.exp(inpZ_shift);
  var expZSum = math.sum(expZ, 1);
  var expZSum_reshape = math.reshape(expZSum, [expZSum.size()[0], 1]);
  var expZSumExtend = this.extendArr(expZSum_reshape, 1, cols);
  var outZ = math.dotDivide(expZ, expZSumExtend);
  return outZ;
};

/**
 * onehot 
 * "A", "B", "C",....:list
 */
NN.prototype.classesOfOneHotOutput = function(onehotMat) {
  // var num_classes = onehotMat.size()[1];
  var onehotArr = onehotMat.valueOf();
  var classesArr = [];
  onehotArr.forEach(item => {
    var cl = this.classOfAOnehot(item);
    classesArr.push(cl);
  });
  return classesArr;
};
/**
 * list 
 * 1. list;
 * 2. "A", "B",...
 */
NN.prototype.classOfAOnehot = function(aOnehot) {
  var classesStore = ["A", "B", "C", "D", "E", "F", "G", "H", "I", "J"];
  var maxOfRow = Math.max(...aOnehot);
  var idxOfMax = aOnehot.indexOf(maxOfRow); // num
  var cl = classesStore[idxOfMax];
  return cl;
};

/**
 *  
 * @param X 
 * @param W
 * @param b 
 */
NN.prototype.calcProbInFP = function(X, W, b) {
  // Z1 = X*W + b
  var Z1 = math.add(math.multiply(X, W[1]), b[1]); // (n,4)
  // console.log("Z1:", Z1);
  var A1 = this.sigmoid(Z1); // (n,4)
  // console.log("A1:", A1);

  var Z2 = math.add(math.multiply(A1, W[2]), b[2]); // (n,3)
  // console.log("Z2:", Z2);
  var A2 = this.softmax(Z2); // (n,3)
  // console.log("A2:", A2);
  return A2;
};

/**
 * NN
 * 
 * inputX: scaling
 * inputYonehot
 * @param layerList NN [ FeaturesL1L2]
 */
NN.prototype.modelTrainCV = function(
  layerList,
  stepSize,
  stepTotal,
  optimizer,
  lambda,
  isLogW,
  gxLen
) {
  this.layerList = layerList; //  , [numOfFeatures, m1,m2,..., numOfClass]
  // list bias
  var len_layerList = this.layerList.length; //  len - 2
  // var numOfFeatures = this.layerList[0];
  // var numOfClass = this.layerList[len_layerList - 1];

  this.stepSize = stepSize || 0.1;
  this.stepTotal = stepTotal || 100;
  this.optimizer = optimizer || "GD";
  this.lambda = lambda || 0.01; // 
  this.isLogW = isLogW || true;
  this.gxLen = gxLen || 100;

  var X = this.inputX; // 22Features
  var XT = math.transpose(X);
  var Y = this.inputY; // 33

  //  W,b
  var W = []; //  W w, [empty, [], [],...]
  var b = []; //  b b, [empty, [], [],...]
  for (let i = 1; i < len_layerList; i++) {
    //  0L0, L1, L2
    W[i] = math.random([layerList[i - 1], layerList[i]], -1, 1);
    b[i] = math.random([1, layerList[i]], -1, 1); // bnn
  }

  for (let i = 0; i < this.stepTotal; i++) {
    // =================  ====================
    //  X

    // L1
    // var Z1 = X * W[1] + b[1]; // (n,4)
    // var b = this.extendArr(b[i],0,X.size()[0]);
    var Z1 = math.add(
      math.multiply(X, W[1]),
      this.extendArr(math.matrix(b[1]), 0, X.size()[0])
    ); // (n,4)
    var A1 = this.sigmoid(Z1); // (n,4)
    // console.log("A1:", A1);

    // L2
    // var Z2 = A1 * W[2] + b[2];
    var Z2 = math.add(
      math.multiply(A1, W[2]),
      this.extendArr(math.matrix(b[2]), 0, X.size()[0])
    ); // (n,3)
    var A2 = this.softmax(Z2); // (n,3)
    // softmax
    // console.log("A2:", A2);

    // ==================  =========================
    var A1T = math.transpose(A1);
    // var delta2 = A2i - 1; //  label   [a,b-1,c] label 
    // ============== ?????  1label elementwise  label onehot====================
    // 1
    // var delta2 = math.dotMultiply(Y, math.subtract(A2, 1)); // (n,3)
    // console.log("delta2:", delta2);

    // 2 ==========================
    // console.log("A2:", A2);

    // ================= ===
    var delta2 = math.subtract(A2, Y); // 

    // console.log("delta2_temp:", delta2_temp);
    // var delta2 = math.dotMultiply(Y, delta2_temp);
    // console.log("delta2:", delta2);
    //

    // var dW2 = A1T * delta2 + lambda * W[2];
    var dW2 = math.add(
      math.multiply(A1T, delta2),
      math.dotMultiply(this.lambda, W[2])
    ); // (4,3)
    // var db2 = delta2; // (n,3)
    var db2 = math.sum(delta2, 0);
    math.reshape(db2, [1, db2.size()[0]]);

    // var delta1 = delta2 * W2T .* ( sz1*(1-sz1) );
    var delta1_left = math.multiply(delta2, math.transpose(W[2])); // (n,3)(3,4)=(n,4)
    var sigmoid_Z1 = this.sigmoid(Z1); // (n,4)
    var delta1_right = math.dotMultiply(
      sigmoid_Z1,
      math.subtract(1, sigmoid_Z1)
    ); // (n,4)
    var delta1 = math.dotMultiply(delta1_left, delta1_right); // (n,4)
    // console.log("delta1:", delta1);
    // var dW1 = XT * delta1 + lambda * W[1];
    var dW1 = math.add(
      math.multiply(XT, delta1), // (2,n)(n,4)=(2,4)
      math.multiply(this.lambda, W[1]) // (2,4)
    ); // (2,4)
    // var db1 = delta1; // (n,4)
    var db1 = math.sum(delta1, 0);
    math.reshape(db1, [1, db1.size()[0]]);

    // console.log("dW2:", dW2);
    // console.log("db2:", db2);
    // console.log("dW1:", dW1);
    // console.log("db1:", db1);

    // ==============  Wb ==================
    // W -= this.stepSize * dW
    // console.log("W[2]:", W[2]);
    W[2] = math.subtract(W[2], math.multiply(this.stepSize, dW2));
    b[2] = math.subtract(b[2], math.multiply(this.stepSize, db2));
    W[1] = math.subtract(W[1], math.multiply(this.stepSize, dW1));
    b[1] = math.subtract(b[1], math.multiply(this.stepSize, db1));
    // console.log("W[2]:", W[2]);
  }
  // console.log("Z2:", Z2);
  // console.log("A2:", A2);
  // console.log("W[2]:", W[2]);
  // console.log("b[2]:", b[2]);
  // console.log("W[1]:", W[1]);
  // console.log("b[1]:", b[1]);

  //  Wb

  this.W = W;
  this.b = b;
  // this.logWval = [W.valueOf()]; // W
  // // W
  // var dCdWArr = []; //  dCdW gt.
  // var dWArr = []; //  dW Adadelta
  // for (var i = 0; i < this.stepTotal; i++) {
  //   var Z = math.multiply(X, W); // Z = XW

  //   var H = this.sigmoid(Z);
  //   var HminusY = math.subtract(H, Y);
  //   var dCdW = math.multiply(XT, HminusY); // ML dCdW 
  //   // console.log("dCdW:", dCdW);

  //   if (optimizer == "GD" || optimizer == 0) {
  //     // , W -= alpha * dCdW
  //     // W = math.subtract(W, math.multiply(this.stepSize, dCdW));
  //     W = this.GradientDescent(W, this.stepSize, dCdW);
  //   } else if (optimizer == "RMSProp" || optimizer == 1) {
  //     // RMSProp
  //     W = this.RMSProp(W, this.stepSize, dCdW, dCdWArr); // 1
  //     dCdWArr.push(dCdW.valueOf());

  //     // dCdW
  //     while (dCdWArr.length > this.gxLen) {
  //       dCdWArr.shift();
  //     }
  //   } else if (optimizer == "Adadelta" || optimizer == 2) {
  //     var obj = this.Adadelta(W, this.stepSize, dCdW, dCdWArr, dWArr);
  //     W = obj.W;
  //     var dW = obj.dW;

  //     dCdWArr.push(dCdW.valueOf());
  //     while (dCdWArr.length > this.gxLen) {
  //       dCdWArr.shift();
  //     }

  //     dWArr.push(dW.valueOf());
  //     while (dWArr.length > this.gxLen) {
  //       dWArr.shift();
  //     }
  //   }
  //   if (this.isLogW) {
  //     // console.log("i: " + i + ", dCdWArr.length: " + dCdWArr.length);
  //     // 
  //     this.logWval.push(W.valueOf());
  //   }
  // }
  // this.optW = W;
  // this.optWval = W.valueOf();
  // this.optWdetails = this.calcWdetails(this.optWval);
  return this;
};

